import React, { useState, useEffect } from 'react';
import {fetchMonthlyData} from '../../Api';
import {Line, Bar} from 'react-chartjs-2';
import { red } from '@material-ui/core/colors';
import Styles from './Charts.module.css';

const Charts = ( {data: { confirmed, recovered, deaths}, country}) => {
    const[daily, setDaily] = useState([]);

    useEffect(() => {
        const fetchMonthly = async () => {
            setDaily(await fetchMonthlyData());  

        }

        fetchMonthly();
    },[]);

    const lineChart = (
        daily.length
        ? (
            <Bar className={Styles.chart}
            data={{
                labels:daily.map(({date}) => date),
                labels:['Confirmed','Recovered', 'Deaths'],
                    datasets: [{
                    data: daily.map(({confirmed}) => confirmed),
                    data: daily.map(({recovered}) => recovered),
                    data: daily.map(({deaths}) => deaths), 
                    backgroundColor:["red",'green','grey'],
                    data:[confirmed.value, recovered.value,deaths.value], 
                    fill:true,
                }],
            }}
                options={{
                    legend: { display: false },
                    title: { display: true, text:`current state in the world`, fontColor:'white', fontSize:12},
                    scales: {
                        yAxes: [{
                            ticks:{
                                fontColor:'white',
                                        }
                                }],
                                xAxes: [{
                                    ticks:{
                                fontColor:'white',
                                        }
                                }],
                            },
                }}
        />) : null
    )

    const BarChart = (
        confirmed
        ? (
            <Bar 
                data={{
                    labels:['confirmed', 'recovered', 'deaths'],
                    datasets:[{
                        label:"people",
                        backgroundColor: ['red','green','grey'],
                        data:[confirmed.value, recovered.value, deaths.value]
                    }],
                    
                }}
                options={{
                    legend: { display: false },
                    title: { display: true, text:`current state in ${country}`},
                }}
            /> ): null
    )

    return(
        <>
        <div className={Styles.container}>
            {country ? BarChart : lineChart}
        </div>
        </>
    )
}

export default Charts;